<template>
  <div class="page-container">
    <div class="page-box accent">
      <div class="page-box-title">
        <h2>Klanten</h2>
        <div class="flex-divider"></div>
        <base-button @click="openCreateModal" class="small-on-mobile">
          <base-icon iconRef="plus" position="left" />
          <span class="button-label">Nieuwe Klant</span>
        </base-button>
      </div>
      <div class="page-box-datatable-container">
        <ais-instant-search :search-client="searchClient" index-name="prod_CLIENTS">
        <ais-search-box
          :class-names="{
            'ais-SearchBox': 'px-6 py-4',
            'ais-SearchBox-form': 'relative',
            'ais-SearchBox-input':
              'w-full py-3 pl-12 pr-3 text-sm font-medium text-teal-900 border-gray-200 rounded shadow-inner bg-cool-gray-100 focus:outline-none focus:shadow-outline focus:border-teal-500',
            'ais-SearchBox-submit':
              'absolute top-0 left-0 flex flex-col justify-center h-full pl-4 text-gray-600 pointer-events-none',
            'ais-SearchBox-submitIcon': 'h-4 w-4',
            'ais-SearchBox-reset':
              'absolute top-0 right-0 flex flex-col justify-center h-full pl-4 pr-4 text-gray-600',
            'ais-SearchBox-resetIcon': 'h-3 w-3',
          }"
        />
        <ais-hits>
          <button
            slot="item"
            slot-scope="{ item }"
            class="client-list-item"
            @click="navigateToClient(item.objectID)"
          >
            <div class="flex flex-col flex-1 text-sm client-list-item--names">
              <div class="client-list-item--client-name font-semibold text-teal-500">{{ item.clientName }}</div>
              <div class="client-list-item--contact-name font-medium">
                {{
                item.contactFirstName || item.contactLastName
                ? item.contactFirstName + " " + item.contactLastName
                : "-"
                }}
              </div>
            </div>
            <div class="w-32 text-sm font-medium client-list-item--date">
              {{
              new Date(item.createdAt._seconds * 1000).toLocaleDateString(
              "nl-NL",
              {
              day: "numeric",
              month: "long",
              year: "numeric",
              }
              )
              }}
            </div>
            <div class="w-40 text-sm font-medium client-list-item--sales-owner">{{ item.salesOwner }}</div>
          </button>
        </ais-hits>
        <ais-pagination
          :class-names="{
            'ais-Pagination': 'py-4 flex justify-center w-full border-t',
            'ais-Pagination-list': 'flex',
            'ais-Pagination-item':
              'rounded overflow-hidden hover:bg-teal-100 hover:text-teal-800',
            'ais-Pagination-item--selected': 'bg-teal-100 text-teal-800',
            'ais-Pagination-link':
              'h-10 w-10 flex items-center justify-center font-semibold cursor-pointer',
          }"
        />
      </ais-instant-search>
      </div>
    </div>
  </div>
</template>

<style lang="scss">
.ais-SearchBox {
  padding: 16px 24px;
}

.ais-SearchBox-form {
  position: relative;
}

.ais-SearchBox-input {
  width: 100%;
  padding: 12px 12px 12px 48px;
  border: 1px solid #999;
  &:focus {
    outline: none;
    border-color: #009ad9;
  }
}

.ais-SearchBox-submit {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  padding-left: 16px;
  pointer-events: none;
}

.ais-SearchBox-submitIcon {
  height: 16px;
  width: 16px;
}

.ais-SearchBox-reset {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  padding: 0 16px;
}

.ais-SearchBox-resetIcon {
  height: 12px;
  width: 12px;
}

.client-list-item {
  display: grid;
  grid-template-columns: 3fr 1fr 1fr;
  align-items: center;
  width: 100%;
  padding: 8px 24px;
  text-align: left;
  border: none;
  background: #fff;
  border-top: 1px solid #e9e9e9;
  cursor: pointer;
  &:hover {
    background: #eefaff;
  }
}

.client-list-item--names {
  display: flex;
  flex-direction: column;
}

.client-list-item--client-name {
  font-weight: bold;
  color: #009ad9;
}

.client-list-item--contact-name {
  font-weight: 600;
  margin-top: 4px;
  color: #595959;
}

.client-list-item--date {
  font-weight: 600;
  color: #595959;
}

.client-list-item--sales-owner {
  font-weight: 600;
  color: #595959;
}

.ais-Pagination {
  border-top: 1px solid #eee;
}

.ais-Pagination-list {
  display: flex;
  width: 100%;
  justify-content: center;
}

.ais-Pagination-item:not(:last-child) .ais-Pagination-link {
  border-right: 1px solid #eee;
}

.ais-Pagination-link {
  height: 48px;
  width: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>

<script>
import algoliasearch from "algoliasearch/lite";

export default {
  name: "Clients",
  data() {
    return {
      searchClient: algoliasearch(
        "B9KCQKP8E1",
        "0382472d88154c2158abc0dc678ade85"
      )
    };
  },
  computed: {
    clientsList() {
      return this.$store.getters.getClientsList.filter(client => {
        return client.data().trashed == false;
      });
    }
  },
  methods: {
    openCreateModal() {
      this.$store.commit("setModalShown", {
        dataType: "client",
        modalType: "create",
        shown: true
      });
    },
    dataEntryClick(value) {
      this.openClient(value);
    },
    navigateToClient(targetClientId) {
      this.$router.push(`/klant/${targetClientId}`);
    }
  },
  mounted() {},
  created() {
    this.$setPageTitle("Klanten");
  }
};
</script>



